            title: `Omori - PTSD/wyparcie`,
            titleImg:`https://i.postimg.cc/3xk96Rz5/capsule-616x353.jpg`,
            text: `Krótkie intro: Omori jest to gra, w której doświadczamy świat jako osoba (jak się wydaje na pierwszy rzut oka) z depresją czy podobnym zaburzeniem. Jako bohater mamy 2 osobowości - prawdziwą i ochronną. Cała akcja rozgrywa się na przemian w świecie prawdziwym, jak i w tak zwanym 'white spacie', które jest swego rodzaju kaftanem ochronnym na mózg, gdzie bohater konserwuje swoje myśli, pragnienia, dzieciństwo, traumy itd. Wszystko to współgra w akompaniamencie cudnej, klimatycznej muzyki, oraz przeplatanką abstrakcyjnych i smutnych scen.`,
            text2:` Cała ta składanka tworzy piękny obraz życia pogrążonego w schizofrenicznej samotności i strachu. Gdzie przeżywamy dziecięce historie, które z razu na raz wchodzą w coraz większą psychodelę i na coraz "doroślejsze" tory. Ale to tylko tło, niestety i na szczęście. Głównym zadaniem tytułowego Omoriego, jest znalezienie swojego kochanego przyjaciela Basila, którego po krótkim spotkaniu na początku nikt nie może znaleźć. Zniknięcie Basila jest zapalnikiem, aby przyjaciele ruszyli go szukać, a co za tym idzie - przeżywali rozmaite przygody. Teraz po kolei... Wszystko zaczyna się od zwykłych zabaw i eksplorowania świata powstałego w white spacie Omoriego, stopniowo przechodzimy coraz dalej łamiąc lęki głównego bohatera, zżywając się z bohaterami, stopniowo również natykamy się na dziwne rzeczy zwiastujące jakąś cholernie zagmatwaną tajemnicę. Szybko się uczymy w grze, że gigantycznym (i bosko uwrażliwiającym) elementem narracji są zdjęcia zrobione przez Basila. Wszystko jest fajnie... taa, poza tymi creepy momentami, gdzie zaczynamy widzieć niestworzone istoty, albo gra na wyjście z white space'u daje nam tylko i wyłącznie możliwość dźgnięcia się nożem w brzuch. Mówiłem, że robi się coraz mocniej, ale jednego jesteśmy pewni ten śmieszny świat jest tylko gównianą fasadą, a tam w rzeczywistości stało się coś piekielnie strasznego, no ale Omori boi się żyć w realnym świecie... Dlatego decydujemy się mu pomóc przeżyć te wszystkie przygody, najwyraźniej tego właśnie potrzebuje.`,
            text3:`Po wszystkich przejściach powoli zaczyna się punkt kulminacyjny, czyli powoli dowiadujemy się co się tak naprawdę stało! Basil w realnym świecie nie wychodzi z domu, przyjaciele są od siebie oddzieleni i jest między nimi masa niedopowiedzeń, no aż w końcu kładziemy jednego z większych puzzli do tej układanki, starsza siostra Omoriego, Marie, nie żyje. Wiedząc już to często czuć niesmak wracając do white space'u, widząc Marie, która zawsze była zwiastunem bezpieczeństwa, czujemy się lekko... jakby to powiedzieć... dziwnie... No ale co, powoli też zaczynamy się bać, że Basil popełnił samobójstwo, dlatego szukanie go w white spacie staje się trochę nie do wytrzymania. Już nie przeciągając zaczynamy się powoli dowiadywać czemu to wszystko tak wygląda, dowiadujemy się czemu white space jest tak ważnym miejscem i jak to się stało, że Marie umarła. Okazuje się, że winni śmierci Marie jesteśmy właśnie my (Omori). Zdjęcia z albumu Basila pokazują nam sceny jak kłótnia Marie i Omoriego powoduje roztrzaskanie ukochanych skrzypiec, które Omori dostał od wszystkich przyjaciół na święta. Niestety dzieje się tak, że Omori popycha w szale Marie ze schodów i przypdakowo ją zabija, po czym zmartwiony kładzie ją do łóżka z nadzieją, że jest po prostu zmęczona (makabryczna scena do wizualizacji w głowie, chwyta za serce za mocno). Widzi to nie kto inny jak Basil... Oczywiście sytuacja jest chora i dramatyczna, ale no przyjaciele decydują się upozorować samobójstwo Marie i wieszają ją na drzewie przed domem. Chore gówno.`,
            text4:`Reszta tej historii to powolne łączenie przyjaciół i wątków, oraz co najważniejsze walka głównego bohatera o siebie i Basila. Najpierw będąc w domu Basila, dostajemy szansę, żeby go uratować i dać mu nadzieję na lepsze jutro. Po czym musimy uciekać z white space'u i walczyć z naszym alter ego czyli czarno-białym Omorim. Kończąc ten cały temat muszę powiedzieć parę rzeczy. Mianowicie jest to piękna i obrazowa historia. Banda dzieci, po przejściach, pozbawiona całkowicie pomocy dorosłych. Omori nigdy nieuczestniczący w rozmowach, żyjący jako pasywny obserwator, który wydaje się niczym jak tylko zapchaj dziurą tej historii. Śmieszne i melancholijne wątki, które z każdym uśmiechem zamieniają się w coraz większe piekło. Najgorsze jest to uczucie, przeżycia kilku ponad lat w swojej głowie, uciekając od tego co się stało, będąc tylko i wyłącznie samemu z wyobrażeniem swoich przyjaciół, którzy jak i ty wiecznie są dziećmi.. `,
