            text:`Taki tytuł ma książka jednego z moich ulubionych neurologów, a mianowicie profesora Jerzego Vetulaniego. Ale ja tutaj nie o tym. Dziś porozmawiamy sobie torszkę o pasji, no i troszkę też o motywacji.`,
            text2:`Będąc niczym w końcu po czasie, zostajemy zesłani tutaj, na ziemię. Budzimy się, nie kumamy nic oprócz "czegoś" co nas dziwnie irytuje ale i intryguje. Tak, chodzi o otoczenie, powietrze, dźwięk, zapach, po prostu każdy nasz niewykształcony zmysł szaleje. Teraz żmudne lata nauki, tak idzie, rok, drugi. Będąc ciągle gdzieś zawieszonym między powinnością, a niewiedzą błądzimy starając się z całych sił pojąc ten zewnętrzny byt. Czasem jest fajnie, cieszymy się chwilą, czasem jest dziwnie i tak chłodno w brzuchu, no ale idziesz dalej. Tym sposobem przechodzimy przez okresy krytyczne, uczymy się, czasem degradujemy nasz mózg, ale i tak powinniśmy wiedzowo wychodzić na plus. `,
            text3:`W pewnym wieku wszystko się obraca o 180 stopni i tracimy coś takiego jak "muszę coś zrobić", na rzecz... no właśnie, mogę lub powinienem coś zrobić. Znowu rozwidlenie.. Wybieramy między lenistwem, ciekawością i chciwością. Lenistwo najszybciej zjada zasoby poznawcze, bo otoczenie i własna satysfakcja nie dają żyć na dłuższą metę. Chciwość sprowadza na manowce też najczęściej z powodu otoczenia, ale jednym z potężnych czynników jest nadmierne tempo eksploracji przyjemności, po którym następuje nuda. To jest akurat bardzo ciekawy temat, ponieważ jak wiadomo, im szybciej zdobywa się gratyfikację (tutaj gratyfikacja jako dążenie do przyjemności) na każdym polu, tym słabsza staję się przyjmność płynąca z tej aktywności. Końcowo trafiamy na ścnianę której nie przeskoczy nasza chciwość, nie mamy już niczego co dawałoby ciągle i ciągle nowego kopa naszemu umysłowi. Wyniki tego są różne, ale jak smutne jest życie wiodące przez chciwość. `,
